:root {
  --bg: unset;
  --fg: black;
  --opac-bg: rgba(0, 0, 0, 0.7);
  --btn-bg: #61adad;
  --btn-active-bg: #62e2e2;
  --nav: linear-gradient(to bottom, cyan -120%, transparent);
  --list-bg: antiquewhite;
}
.dark {
  --bg: #000;
  --fg: white;
  --opac-bg: rgba(255, 255, 255, 0.7);
  --btn-bg: #202020;
  --btn-active-bg: #3f3f3f;
  --list-bg: #2c2929;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  width: 100%;
  height: 100vh;
  background: var(--bg);
  color: var(--fg);
  transition: background 0.5s, all 1s;
}
nav {
  width: 100%;
  height: 15vh;
  background: var(--nav);
  display: flex;
  justify-content: center;
  align-items: center;
}
nav h1 {
  font-size: calc(5vw + 16px);
  text-transform: uppercase;
  font-family: 'Posterama';
  cursor: pointer;
  user-select: none;
}
.btns {
  width: 95%;
  height: 5vh;
  margin: auto;
}
.btns button {
  position: relative;
  width: 50%;
  height: 100%;
  border: none;
  outline: none;
  transition: 0.3s;
  font-size: 100%;
  border-radius: 5px 5px 0 0;
  background: var(--btn-bg);
  font-weight: 700;
  color: var(--fg);
}
.btns button:active {
  background: palevioletred;
}
.btns button.active {
  transition: 0.3s;
  z-index: 10;
  background: var(--btn-active-bg);
  border: 2px solid palevioletred;
  border-color: palevioletred palevioletred var(--btn-active-bg) palevioletred;
}
main {
  margin: auto;
  transform: translateY(-2px);
  width: 95%;
  min-height: 70vh;
  border: 2px solid palevioletred;
  background: var(--btn-active-bg);
  border-radius: 0 0 5px 5px;
}
section ul {
  list-style: none;
}
section ul li {
  margin: 10px auto;
  padding: 2vw 1vw;
  width: 95%;
  height: 3.5em;
  background: var(--list-bg);
  border-radius: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background 0.1s, all 0.1s;
}
section ul li img {
  height: 40px;
  margin-right: 2px;
}
section ul li .name,
section ul li .price {
  margin-right: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
@media screen and (max-width: 480px) {
  section ul li .name,
  section ul li .price {
    font-size: 1.2em;
  }
}
section ul li .name span,
section ul li .price span {
  text-transform: uppercase;
  font-size: 0.7em;
  opacity: 0.7;
}
section ul li .price {
  margin: 0;
  align-items: flex-end;
  margin-left: auto;
}
section ul li .price span {
  font-size: 0.8em;
}
section ul li .watch {
  width: 40px;
  height: 40px;
  clip-path: polygon(40% 0%, 40% 20%, 100% 20%, 100% 80%, 40% 80%, 40% 100%, 0% 50%);
  border: none;
  outline: none;
  box-shadow: 0 0 25px black inset;
  transform: rotate(90deg);
  background: #42feb8;
}
section ul li .watch.active {
  background: hsl(7, 98%, 54%);
  transform: rotate(-90deg);
}
